import React from 'react';
import { Link } from 'react-router-dom';

const getPaymentType = type => {
    let a = '';
    switch (type) {
        case 'onlineCard':
            a = '–ö–∞—Ä—Ç–æ–π –æ–Ω–ª–∞–π–Ω';
            break;
        case 'offlineCard':
            a = '–ö–∞—Ä—Ç–æ–π –∫—É—Ä—å–µ—Ä—É';
            break;
        case 'offlineCash':
            a = '–ù–∞–ª–∏—á–Ω—ã–º–∏ –∫—É—Ä—å–µ—Ä—É';
            break;
        default:
            a = '–ù–µ –≤—ã–±—Ä–∞–Ω–æ';
    }
    return a;
};

